<template>
    <div class="page-container">
        <el-aside class="page-nav-left">
            <div class="aside-bar">
                <el-menu default-active="0">
                    <router-link to="/farming/task">
                        <el-menu-item index="0">
                            <i class="el-icon-date"></i>
                            <span slot="title">任务管理</span>
                        </el-menu-item>
                    </router-link>
                    <router-link to="/farming/seed">
                        <el-menu-item index="1">
                            <i class="el-icon-coin"></i>
                            <span slot="title">种子管理</span>
                        </el-menu-item>
                    </router-link>
                    <router-link to="/farming/tour">
                        <el-menu-item index="2">
                            <i class="el-icon-truck"></i>
                            <span slot="title">巡园管理</span>
                        </el-menu-item>
                    </router-link>
                    <router-link to="/farming/work">
                        <el-menu-item index="3">
                            <i class="el-icon-notebook-1"></i>
                            <span slot="title">工作汇报</span>
                        </el-menu-item>
                    </router-link>
                </el-menu>
            </div>
        </el-aside>
        <el-main class="m-l-220 m-t-20 bg-white">
            <div class="flex flex-align-center flex-pack-justify">
                <div class="flex flex-align-center">
                    <el-select class="m-r-15" size="small" style="width:200px;" v-model="vm.searchType" placeholder="请选择类型">
                        <el-option
                            v-for="item in farmTypeList"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                    <el-input class="m-r-15" size="small" style="width:200px;" placeholder="请输入任务" suffix-icon="el-icon-search" v-model="vm.searchKey"></el-input>
                    <el-button class="m-r-30" type="primary" size="small" @click="search">搜索</el-button>
                    <!-- <div>
                        <el-switch v-model="vm.filter1" active-text="显示未完成任务"></el-switch>
                        <el-switch v-model="vm.filter2" active-text="显示已完成的任务"></el-switch>
                        <el-switch v-model="vm.filter3" active-text="显示已忽略任务"></el-switch>
                    </div> -->
                </div>
                <el-button type="primary" size="small" @click="add">添加任务信息</el-button>
            </div>
            <el-row class="m-t-30" :gutter="15">
                <div class="task-list">
                    <template v-for="(item,index) in dataList">
                        <el-col :xs="24" :sm="12" :md="12" :lg="8" :key="index">
                            <div class="task-item"  @click="edit(item)">
                                <div class="flex flex-align-center flex-pack-justify">
                                    <div class="flex flex-align-center">
                                        <el-tag effect="dark" size="mini" class="m-r-15" v-if="item.taskType == 1">{{item.farmType}}</el-tag>
                                        <el-tag effect="dark" size="mini" class="m-r-15" type="success" v-else>{{item.farmType}}</el-tag>
                                        <span class="task-type">{{item.taskTypeCn}}</span>
                                    </div>
                                    <div class="task-level">优先级：{{item.taskLevelCn}}
                                        <i class="el-icon-warning" style="color:#44BBEE" v-if="item.taskLevel < 3"></i>
                                        <i class="el-icon-warning" style="color:#FFBA00" v-if="item.taskLevel == 3"></i>
                                        <i class="el-icon-warning" style="color:#ff4949" v-if="item.taskLevel > 3"></i>
                                    </div>
                                </div>
                                <div class="task-desc"><strong>{{item.taskTitle}} — </strong>{{item.taskCon}}</div>
                                <div class="flex flex-align-center flex-pack-justify">
                                    <span class="task-plot">{{item.plot}}</span>
                                    <el-tag type="info">{{item.statusCn}}</el-tag>
                                </div>
                                <div class="task-time">{{item.startTime}}至{{item.endTime}}</div>
                            </div>
                        </el-col>
                    </template>
                </div>
            </el-row>
            <el-row class="m-t-30">
                <el-col>
                    <el-pagination 
                        class="text-right" 
                        background 
                        layout="total, prev, pager, next" 
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page.sync="vm.currentPage"
                        :page-size="vm.pageSize"
                        :total="vm.pageTotal">
                    </el-pagination>
                </el-col>
            </el-row>
            <el-dialog title="任务信息" :visible.sync="dialogVisible" width="800px" :close-on-click-modal="false">
                <el-form ref="form" size="small" :rules="rules" :model="form" label-width="150px">
                    <el-form-item label="农事类型" prop="farmType">
                        <el-select style="width:260px;" v-model="form.farmType" placeholder="请选择农事类型">
                            <el-option
                                v-for="item in farmTypeList"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="任务标准" prop="taskType">
                        <el-select style="width:260px;" v-model="form.taskType" placeholder="请选择任务类型">
                            <el-option
                                v-for="item in taskTypeList"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="优先级" prop="taskLevel">
                        <el-rate style="margin-top: 6px;" v-model="form.taskLevel" :icon-classes="['el-icon-warning', 'el-icon-warning','el-icon-warning']" void-icon-class="el-icon-warning" :colors="['#39bc54','#FFBA00','#F56C6C']" :texts="['Ⅰ级','Ⅱ级','Ⅲ级','Ⅳ级','Ⅴ级']" show-text></el-rate>
                    </el-form-item>
                    <el-form-item label="开始时间" prop="startTime">
                        <el-date-picker style="width:260px;" type="datetime" placeholder="选择日期" v-model="form.startTime"></el-date-picker>
                    </el-form-item>
                    <el-form-item label="结束时间" prop="endTime">
                        <el-date-picker style="width:260px;" type="datetime" placeholder="选择日期" v-model="form.endTime"></el-date-picker>
                    </el-form-item>
                    <el-form-item label="派发人员" prop="taskWorker">
                        <el-select style="width:260px;" v-model="form.taskWorker" filterable multiple placeholder="请选择派发人员">
                            <el-option
                                v-for="item in taskWorkerList"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="用工量（人天）" prop="taskNumber">
                        <el-input style="width:260px;" v-model="form.taskNumber"></el-input>
                    </el-form-item>
                    <el-form-item label="标题" prop="taskTitle">
                        <el-input style="width:260px;" v-model="form.taskTitle"></el-input>
                    </el-form-item>
                    <el-form-item label="内容" prop="taskCon">
                        <el-input type="textarea" v-model="form.taskCon"></el-input>
                    </el-form-item>
                </el-form>
                <span slot="footer" class="dialog-footer">
                    <el-button size="small" @click="dialogVisible = false">取消</el-button>
                    <el-button size="small" type="primary" @click="save">保存</el-button>
                </span>
            </el-dialog>
        </el-main>
    </div>
</template>

<script>
export default {
    data(){
        return{
            vm: {
                searchKey: '',
                searchType: '选项0',
                filter1: true,
                filter2: false,
                filter3: false,
                currentPage: 1,
                pageSize: 10,
                pageTotal: 100,
            },
            farmTypeList: [{
                value: '选项0',
                label: '全部任务'
            }, {
                value: '选项1',
                label: '施肥'
            }, {
                value: '选项2',
                label: '施药'
            }, {
                value: '选项3',
                label: '农事'
            }, {
                value: '选项4',
                label: '采集'
            }, {
                value: '选项5',
                label: '巡园'
            }],
            dataList:[{
                farmType: '农事类型',
                taskType: 1,
                taskTypeCn: '标准任务',
                taskLevel: 5,
                taskLevelCn: 'Ⅴ',
                startTime: '2018-1-24',
                endTime: '2018-1-27',
                taskNumber: '',
                taskTitle: '施肥4',
                taskNumber: '5人天',
                taskCon: '全园追肥4斤/亩',
                plot: '东里旺旺葡萄园',
                status: 0,
                statusCn: '任务未开始',
            },{
                farmType: '农事类型',
                taskType: 2,
                taskTypeCn: '临时任务',
                taskLevel: 3,
                taskLevelCn: 'Ⅲ',
                startTime: '2018-1-24',
                endTime: '2018-1-27',
                taskNumber: '',
                taskTitle: '施肥4',
                taskNumber: '2人天',
                taskCon: '全园追肥',
                plot: '东里旺旺葡萄园',
                status: 0,
                statusCn: '任务未开始',
            }],
            dialogVisible: false,
            form:{
                farmType: '',
                taskType: '',
                taskLevel: null,
                startTime: '',
                endTime: '',
                taskWorker: [],
                taskNumber: '',
                taskTitle: '',
                taskCon: ''
            },
            taskTypeList:[{
                value: '选项0',
                label: '标准任务'
            }, {
                value: '选项1',
                label: '临时任务'
            }],
            taskWorkerList:[{
                value: '选项0',
                label: '人员1'
            }, {
                value: '选项1',
                label: '人员2'
            }, {
                value: '选项2',
                label: '人员3'
            }],
            rules: {
                farmType: [
                    { required: true, message: '请选择农事类型', trigger: 'change' },
                ],
                taskType: [
                    { required: true, message: '请选择任务类型', trigger: 'change' },
                ],
                taskLevel: [
                    { required: true, message: '优先级', trigger: 'change' },
                ],
                startTime: [
                    { type: 'date', required: true, message: '请选择日期', trigger: 'change' }
                ],
                endTime: [
                    { type: 'date', required: true, message: '请选择日期', trigger: 'change' }
                ],
                taskWorker: [
                    { required: true, message: '派发人员', trigger: 'change' },
                ],
                taskNumber: [
                    { required: true, message: '请填写用工量（人天）', trigger: 'blur' }
                ],
                taskTitle: [
                    { required: true, message: '请填写标题', trigger: 'blur' }
                ],
                taskCon: [
                    { required: true, message: '请填写内容', trigger: 'blur' }
                ]
            }
        }
    },
    methods: {
        search(){

        },
        add(){
            this.dialogVisible = true
            this.resetForm()
        },
        edit(row) {
            this.resetForm()
            this.dialogVisible = true;
            this.form = row
        },
        save(){
            this.$refs['form'].validate((valid) => {
                if (valid) {
                    console.log(this.form)
                    this.dialogVisible = false
                } else {
                    this.$message.error("请检查您的输入！");
                    return false;
                }
            });
        },
        resetForm() {
            this.form = {
                farmType: '',
                taskType: '',
                taskLevel: null,
                startTime: '',
                endTime: '',
                taskWorker: [],
                taskNumber: '',
                taskTitle: '',
                taskCon: ''
            }
        },
        handleCommand(command) {
            console.log(command);
        },
        handleSizeChange(val) {
            console.log(val);
        },
        handleCurrentChange(val) {
            console.log(val);
        }
    }
}
</script>

<style>

</style>


